import React from 'react';
import { NavLink } from 'react-router-dom';
const Navbar = () => {
    return (
        <>
            <nav className="navbar navbar-expand-lg navbar-light">
                <div className="container-fluid">
                    <NavLink exact className="navbar-brand" to="/">Rahul Maheshwari</NavLink>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                                <NavLink activeClassName="menu_active" exact className="nav-link active" to="/">Home</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink activeClassName="menu_active" exact className="nav-link active" to="/service">Service</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink activeClassName="menu_active" exact className="nav-link active" to="/about">PortFolio</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink activeClassName="menu_active" exact className="nav-link active" to="/project">Projects</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink activeClassName="menu_active" exact className="nav-link active" to="/contact">Contact US</NavLink>
                            </li>
                        </ul>

                    </div>
                </div>
            </nav>


        </>
    )
}
export default Navbar;


